 .ToDoItem{
    background-color: #FAFAFA;
    position: relative;
    display: flex;
    justify-content: center;
    justify-items: center;
    margin-top: 25px;
    box-shadow: 0px 5px 50px rgba(32,35,41,0.15);
}
.ToDoItem-p{
    margin:24px 0 24px 24px;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}
.ToDoItem-p--complete {
    text-decoration: line-through;
}
.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
  }
.Icon-check {
    position: absolute;
    left:12px;
    top:11px;
}
.Icon-check--active{
    color:#4CAF50;
}
.Icon-delete{
    position: absolute;
    top: -16px;
    right: -19px;
    color:white;
    border-radius: 33px;
    border:0;
    background-color: black;
    width: 30px;
    height: 30px;
}
.Icon-delete:hover{
    color: red;
}
.border-x{
    border: 2px solid #202329;
}
